import React, { Component } from 'react'
import { Row, Col, Tabs, Tab, Panel, Table } from 'react-bootstrap'

import G8 from '../data/G8.json'
import Limbo from '../data/limbo.json'
import Nervoso from '../data/nervoso.json'
import Minardi from '../data/minardi.json'

export default class Tabela extends Component {
    constructor(props) {
        super(props)

        this.state = {
            times: {
                G8: this.ordenarTimes(G8),
                Limbo: this.ordenarTimes(Limbo),
                Nervoso: this.ordenarTimes(Nervoso),
                Minardi: this.ordenarTimes(Minardi)
            }
        }
    }

    ordenarTimes(times) {
        return times.sort((a, b) => { return a.posicao - b.posicao })
    }

    render() {
        return (
            <Row>
                <Col xs={12} md={12}>
                    <Tabs defaultActiveKey={1} id="tabs">
                        <Tab eventKey={1} title="#ShareTheDrama">
                            <br />
                            <Panel>
                                <Panel.Heading>
                                    <strong>Nervoso</strong>
                                </Panel.Heading>
                                <Panel.Body>
                                    <ul className="times">
                                        {
                                            this.state.times.Nervoso.map((time) => {
                                                return <li key={time.img}><img alt={time.nome} src={time.img} /></li>
                                            })
                                        }
                                    </ul>
                                    <br />
                                    <Table>
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Time</th>
                                                <th>Pts</th>
                                                <th>V</th>
                                                <th>E</th>
                                                <th>D</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                this.state.times.Nervoso.map((time) => {
                                                    return (
                                                        <tr key={time.nome}>
                                                            <td className={time.classe}>{time.posicao}</td>
                                                            <td>{time.nome}</td>
                                                            <td>{time.pontos}</td>
                                                            <td>{time.vitorias}</td>
                                                            <td>{time.empates}</td>
                                                            <td>{time.derrotas}</td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </Table>
                                </Panel.Body>
                            </Panel>
                            <Panel>
                                <Panel.Heading>
                                    <strong>Minardi</strong>
                                </Panel.Heading>
                                <Panel.Body>
                                    <ul className="times">
                                        {
                                            this.state.times.Minardi.map((time) => {
                                                return <li key={time.img}><img alt={time.nome} src={time.img} /></li>
                                            })
                                        }
                                    </ul>
                                    <br />
                                    <Table>
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Time</th>
                                                <th>Pts</th>
                                                <th>V</th>
                                                <th>E</th>
                                                <th>D</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                this.state.times.Minardi.map((time) => {
                                                    return (
                                                        <tr key={time.nome}>
                                                            <td className={time.classe}>{time.posicao}</td>
                                                            <td>{time.nome}</td>
                                                            <td>{time.pontos}</td>
                                                            <td>{time.vitorias}</td>
                                                            <td>{time.empates}</td>
                                                            <td>{time.derrotas}</td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </Table>
                                </Panel.Body>
                            </Panel>
                        </Tab>
                        <Tab eventKey={2} title="#SemDrama">
                            <br />
                            <Panel>
                                <Panel.Heading>
                                    <strong>G9</strong>
                                </Panel.Heading>
                                <Panel.Body>
                                    <ul className="times">
                                        {
                                            this.state.times.G8.map((time) => {
                                                return <li key={time.img}><img alt={time.nome} src={time.img} /></li>
                                            })
                                        }
                                    </ul>
                                    <br />
                                    <Table>
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Time</th>
                                                <th>Pts</th>
                                                <th>V</th>
                                                <th>E</th>
                                                <th>D</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                this.state.times.G8.map((time) => {
                                                    return (
                                                        <tr key={time.nome}>
                                                            <td className={time.classe}>{time.posicao}</td>
                                                            <td>{time.nome}</td>
                                                            <td>{time.pontos}</td>
                                                            <td>{time.vitorias}</td>
                                                            <td>{time.empates}</td>
                                                            <td>{time.derrotas}</td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                    </Table>
                                </Panel.Body>
                            </Panel>
                            {this.state.times.Limbo.length > 0 &&
                                <Panel>
                                    <Panel.Heading>
                                        <strong>Limbo</strong>
                                    </Panel.Heading>
                                    <Panel.Body>
                                        <ul className="times">
                                            {
                                                this.state.times.Limbo.map((time) => {
                                                    return <li key={time.img}><img alt={time.nome} src={time.img} /></li>
                                                })
                                            }
                                        </ul>
                                        <br />
                                        <Table>
                                            <thead>
                                                <tr>
                                                    <th>#</th>
                                                    <th>Time</th>
                                                    <th>Pts</th>
                                                    <th>V</th>
                                                    <th>E</th>
                                                    <th>D</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    this.state.times.Limbo.map((time) => {
                                                        return (
                                                            <tr key={time.nome}>
                                                                <td className={time.classe}>{time.posicao}</td>
                                                                <td>{time.nome}</td>
                                                                <td>{time.pontos}</td>
                                                                <td>{time.vitorias}</td>
                                                                <td>{time.empates}</td>
                                                                <td>{time.derrotas}</td>
                                                            </tr>
                                                        )
                                                    })
                                                }
                                            </tbody>
                                        </Table>
                                    </Panel.Body>
                                </Panel>
                            }
                        </Tab>
                    </Tabs>
                </Col>
            </Row>
        )
    }
}



