import React, { Component } from 'react'
import { Row, Col, Jumbotron, Media, Panel } from 'react-bootstrap'

export default class Sobre extends Component {
    render() {
        return (
            <div>
                <Row>
                    <Col xs={12} md={6}>
                        <Jumbotron>
                            <Media>
                                <Media.Body>
                                    <Media.Heading>AUDIOGUIA SÉRIE A #414</Media.Heading>
                                    <p>O 45 Minutos está mais do que preparado para a temporada 2018 e nosso audioguia está no ar com muita informação, opinião e a resenha de sempre.</p>
                                </Media.Body>
                            </Media>
                            <iframe title="podcast45" width="100%" height="50" src="https://podcast45minutos.com.br/?powerpress_embed=6975-podcast&amp;powerpress_player=mediaelement-audio" frameBorder="0" scrolling="no"></iframe>
                        </Jumbotron>
                    </Col>
                    <Col xs={12} md={6}>
                        <Jumbotron>
                            <Media>
                                <Media.Body>
                                    <Media.Heading>AUDIOGUIA RELOADED #431</Media.Heading>
                                    <p>
                                        Após a parada da Copa do Mundo muita coisa mudou na Série A.
                                        Aqui no 45 Minutos quase nada, mas com tantas mudanças a gente tinha que fazer um novo audioguia.
                                    </p>
                                </Media.Body>
                            </Media>
                            <iframe title="podcast45" width="100%" height="50" src="https://podcast45minutos.com.br/?powerpress_embed=8091-podcast&amp;powerpress_player=mediaelement-audio" frameBorder="0" scrolling="no"></iframe>
                        </Jumbotron>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} md={12}>
                        <Panel>
                            <Panel.Heading>
                                <strong>Conceitos</strong>
                            </Panel.Heading>
                            <Panel.Body>
                                <p><b>G8:</b> Brigam pelo título e vagas na Libertadores</p>
                                <p><b>Limbo:</b> Não brigam em cima e não caem</p>
                                <p><b>Nervoso:</b> Brigam contra o rebaixamento</p>
                                <p><b>Minardi:</b> Não escapam do rebaixamento</p>
                            </Panel.Body>
                        </Panel>
                    </Col>
                    <Col xs={12} md={12}>
                        <Panel>
                            <Panel.Heading>
                                <strong>Previsão no Podcast #414</strong>
                            </Panel.Heading>
                            <Panel.Body>
                                <p><b>G8:</b> Atlético-MG, Corinthians, Cruzeiro, Flamengo, Grêmio, Palmeiras, São Paulo, Santos</p>
                                <p><b>Limbo:</b> Atlético-PR, Internacional</p>
                                <p><b>Nervoso:</b> América-MG, Bahia, Botafogo, Ceará, Chapecoense, Fluminense, Sport, Vasco, Vitória</p>
                                <p><b>Minardi:</b> Paraná</p>
                            </Panel.Body>
                        </Panel>
                    </Col>
                    <Col xs={12} md={12}>
                        <Panel>
                            <Panel.Heading>
                                <strong>Realinhamento no Podcast #431</strong>
                            </Panel.Heading>
                            <Panel.Body>
                                <p><b>G9:</b> Atlético-MG, Corinthians, Cruzeiro, Flamengo, Grêmio, Internacional, Palmeiras, São Paulo, Santos</p>
                                <p><b>Nervoso:</b> América-MG, Atlético-PR, Bahia, Botafogo, Chapecoense, Fluminense, Sport, Vasco, Vitória</p>
                                <p><b>Minardi:</b> Ceará e Paraná</p>
                            </Panel.Body>
                        </Panel>
                    </Col>
                </Row>
            </div>
        )
    }
}



